.banner{
    height: 448px;
    position: relative;
    color: white;
    object-fit: contain;
}
.banner__contents{
    margin:30px;
    padding-top: 140px;
    height: 190px;
}
.banner__title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner__description{
    width:45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size:0.8rem;
    max-width: 360px;
    height: 80px;
}
