.banner__button{
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color:rgba(107, 61, 61, 0.5);
    padding-bottom: 0.5rem;

}
.banner__button:hover{
    color: #000;
    background-color:#ece2e2;
    transition: all 0.2s;
}

